#chat-display {
    flex-grow: 1;
    height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: #f7f7f7;
}

@media(max-width: 600px) {
    #chat-display {
        height: calc(100vh - 90px);
        top: 80px;
        position: absolute;
        width: 100vw;
    }
}